// src/components/signup.jsx
import React, { useMemo, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import styled from "styled-components";
import {
  requestEmailVerification,
  confirmEmailVerification,
  signUp,
} from "../api/auth";

const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;

const Signup = () => {
  const navigate = useNavigate();

  const [email, setEmail] = useState("");
  const [nickname, setNickname] = useState("");
  const [pwd, setPwd] = useState("");
  const [pwd2, setPwd2] = useState("");
  const [birth, setBirth] = useState(""); // yyyy-mm-dd
  const [gender, setGender] = useState(""); // 'male' | 'female'
  const [job, setJob] = useState("");

  // ()     API    
  const [emailChecked, setEmailChecked] = useState(false);
  const [nickChecked, setNickChecked] = useState(false);

  //   
  const [emailVerifySent, setEmailVerifySent] = useState(false);
  const [emailVerified, setEmailVerified] = useState(false);
  const [lockedEmail, setLockedEmail] = useState(""); //      
  const [code, setCode] = useState("");
  const [sending, setSending] = useState(false);
  const [confirming, setConfirming] = useState(false);

  const [showDone, setShowDone] = useState(false);
  const [submitting, setSubmitting] = useState(false);

  // 
  const emailValid = useMemo(
    () => emailRegex.test(email.trim().toLowerCase()),
    [email]
  );
  const nickValid = useMemo(
    () => nickname.trim().length >= 2 && nickname.trim().length <= 7,
    [nickname]
  );
  const pwdValid = useMemo(() => pwd.trim().length >= 6, [pwd]);
  const pwdSame = useMemo(() => pwd !== "" && pwd === pwd2, [pwd, pwd2]);
  const birthValid = useMemo(() => birth.trim() !== "", [birth]);
  const genderValid = useMemo(
    () => gender === "male" || gender === "female",
    [gender]
  );

  //    
  const canSubmit =
    emailValid &&
    nickValid &&
    pwdValid &&
    pwdSame &&
    birthValid &&
    genderValid &&
    emailChecked &&
    nickChecked &&
    emailVerified;

  //  "" ( API   )
  const handleEmailCheck = () => {
    if (!emailValid) return;
    setEmailChecked(true);
    alert("   (  API ).");
  };

  //  "" ( API   )
  const handleNickCheck = () => {
    if (!nickValid) return;
    setNickChecked(true);
    alert("   (  API ).");
  };

  //   
  const handleSendVerifyEmail = async () => {
    if (!emailChecked || !emailValid || sending) return;
    try {
      setSending(true);
      const normalized = email.trim().toLowerCase(); //   /
      const { data } = await requestEmailVerification(normalized);
      // ResponseDto: { success:boolean, message:string }
      if (data?.success) {
        setLockedEmail(normalized);     //     
        setEmailVerifySent(true);
        setEmailVerified(false);
        alert(data?.message || "  .   .");
      } else {
        setEmailVerifySent(false);
        setEmailVerified(false);
        alert(data?.message || "   .");
      }
    } catch (e) {
      console.error(e);
      alert(" (/CORS/ )");
    } finally {
      setSending(false);
    }
  };

  //  
  const handleConfirmEmail = async () => {
    if (!emailVerifySent || !code.trim() || confirming) return;
    if (!lockedEmail) return alert("   .");
    try {
      setConfirming(true);
      const { data } = await confirmEmailVerification({
        email: lockedEmail, //   lockedEmail 
        code: code.trim(),
      });
      if (data?.success) {
        setEmailVerified(true);
        alert(data?.message || "  .");
      } else {
        setEmailVerified(false);
        alert(data?.message || " .   .");
      }
    } catch (e) {
      console.error(e);
      alert(" (/CORS/ )");
    } finally {
      setConfirming(false);
    }
  };

  //  
  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!canSubmit || submitting) return;

    // birth(yyyy-mm-dd)   ()
    const calcAge = (b) => {
      try {
        const y = parseInt(b.slice(0, 4), 10);
        if (!y) return "";
        const nowY = new Date().getFullYear();
        return String(nowY - y);
      } catch {
        return "";
      }
    };

    const normalized = email.trim().toLowerCase();

    //      
    if (!emailVerified || normalized !== lockedEmail) {
      return alert("  ,     .");
    }

    try {
      setSubmitting(true);
      //  DTO  :   (   OK)
      const payload = {
        email: lockedEmail,
        password: pwd,
        nickname,
        gender,
        birth,              //  
        birthDate: birth,   //     
        age: calcAge(birth),
        job: job || "",
      };

      const res = await signUp(payload);
      const { status, data } = res || {};

      if (data?.success === false) {
        return alert(data?.message || " .");
      }
      if (status >= 400) {
        return alert(" .");
      }

      setShowDone(true);
      setTimeout(() => {
        setShowDone(false);
        navigate("/login");
      }, 1200);
    } catch (err) {
      console.error(err);
      const msg =
        err?.response?.data?.message ||
        " (/CORS/ )";
      alert(msg);
    } finally {
      setSubmitting(false);
    }
  };

  //     
  const onChangeEmail = (v) => {
    setEmail(v);
    setEmailChecked(false);
    setEmailVerifySent(false);
    setEmailVerified(false);
    setLockedEmail(""); //      
    setCode("");
  };
  const onChangeNick = (v) => {
    setNickname(v);
    setNickChecked(false);
  };

  return (
    <Bg>
      <Top>
        <Logo>TingTing</Logo>
        <BackLink to="/login"> </BackLink>
      </Top>

      <Card as="form" onSubmit={handleSubmit}>
        <Brand>TingTing</Brand>
        <Title></Title>

        {/*  */}
        <Inline>
          <Input
            placeholder=""
            value={email}
            onChange={(e) => onChangeEmail(e.target.value)}
            autoComplete="email"
          />
          <SmallBtn
            type="button"
            disabled={!emailValid}
            onClick={handleEmailCheck}
          >
            
          </SmallBtn>
        </Inline>
        {email !== "" && (
          <Hint ok={emailValid}>
            {emailValid
              ? "   "
              : "   ."}
          </Hint>
        )}

        {/*    */}
        {emailChecked && (
          <>
            {!emailVerifySent && !emailVerified && (
              <RightAlign>
                <SmallBtn
                  type="button"
                  onClick={handleSendVerifyEmail}
                  disabled={sending}
                >
                  {sending ? "..." : " "}
                </SmallBtn>
              </RightAlign>
            )}

            {emailVerifySent && !emailVerified && (
              <>
                <Inline>
                  <Input
                    placeholder="  "
                    value={code}
                    onChange={(e) => setCode(e.target.value)}
                  />
                  <SmallBtn
                    type="button"
                    onClick={handleConfirmEmail}
                    disabled={!code.trim() || confirming}
                  >
                    {confirming ? "..." : " "}
                  </SmallBtn>
                </Inline>
                <Hint ok={false}>
                  ( )     <b> </b> .
                </Hint>
              </>
            )}

            {emailVerified && (
              <>
                <RightAlign>
                  <SmallBtn type="button" disabled>
                     
                  </SmallBtn>
                </RightAlign>
                <Hint ok={true}>
                    . ( : {lockedEmail})
                </Hint>
              </>
            )}
          </>
        )}

        {/*  */}
        <Inline>
          <Input
            placeholder=" (2~7)"
            value={nickname}
            onChange={(e) => onChangeNick(e.target.value)}
          />
          <SmallBtn
            type="button"
            disabled={!nickValid}
            onClick={handleNickCheck}
          >
            
          </SmallBtn>
        </Inline>
        {nickname !== "" && (
          <Hint ok={nickValid}>
            {nickValid
              ? "   "
              : " 2~7 ."}
          </Hint>
        )}

        {/*  */}
        <Input
          placeholder=" (6 )"
          type="password"
          value={pwd}
          onChange={(e) => setPwd(e.target.value)}
          autoComplete="new-password"
        />
        <Input
          placeholder=" "
          type="password"
          value={pwd2}
          onChange={(e) => setPwd2(e.target.value)}
          autoComplete="new-password"
        />
        {pwd2 !== "" && (
          <Hint ok={pwdSame}>
            {pwdSame ? " ." : "  ."}
          </Hint>
        )}

        {/*  */}
        <Input
          type="date"
          value={birth}
          onChange={(e) => setBirth(e.target.value)}
        />

        {/*  */}
        <GenderRow>
          <GenderBtn
            type="button"
            $active={gender === "male"}
            onClick={() => setGender("male")}
          >
            
          </GenderBtn>
          <GenderBtn
            type="button"
            $active={gender === "female"}
            onClick={() => setGender("female")}
          >
            
          </GenderBtn>
        </GenderRow>

        <PrimaryBtn type="submit" disabled={!canSubmit || submitting}>
          {submitting ? " ..." : ""}
        </PrimaryBtn>
      </Card>

      {showDone && (
        <ModalBG>
          <ModalCard>  !</ModalCard>
        </ModalBG>
      )}
    </Bg>
  );
};

export default Signup;

/* ===== styles ===== */
const Bg = styled.main`
  min-height: 100vh;
  display: grid;
  place-items: center;
  background: linear-gradient(135deg, #ffb7d5 0%, #ff9ec2 40%, #ffb5d1 100%);
  padding: 24px;
`;

const Top = styled.div`
  position: absolute;
  top: 28px;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
`;

const Logo = styled.h2`
  color: #fff;
  font-size: clamp(24px, 3.8vw, 36px);
  text-shadow: 0 6px 16px rgba(0,0,0,.18), 0 2px 0 rgba(255,255,255,.35);
`;

const BackLink = styled(Link)`
  position: absolute;
  top: 8px;
  right: min(4vw, 24px);
  color: rgba(255,255,255,.95);
  font-weight: 700;
  text-decoration: none;
`;

const Card = styled.section`
  width: min(92vw, 460px);
  background: #fff;
  border-radius: 16px;
  padding: 28px 24px 24px;
  box-shadow: 0 20px 60px rgba(0,0,0,.18);
`;

const Brand = styled.h3`
  color: #ff2f79;
  font-weight: 800;
  text-align: center;
  margin: 0 0 4px;
`;

const Title = styled.h1`
  font-size: 22px;
  text-align: center;
  margin: 0 0 18px;
`;

const Input = styled.input`
  width: 100%;
  height: 44px;
  border-radius: 10px;
  border: 1.5px solid #e6e6e6;
  padding: 0 12px;
  margin-bottom: 10px;
  font-size: 15px;
  &:focus { outline: none; border-color: #ff7aa7; box-shadow: 0 0 0 3px rgba(255, 122, 167, .15); }
`;

const Inline = styled.div`
  display: grid;
  grid-template-columns: 1fr 100px;
  gap: 8px;
  align-items: center;
`;

const SmallBtn = styled.button`
  height: 44px;
  border-radius: 10px;
  border: 2px solid #ff2f79;
  background: #fff;
  color: #ff2f79;
  font-weight: 800;
  cursor: ${({ disabled }) => (disabled ? "not-allowed" : "pointer")};
  opacity: ${({ disabled }) => (disabled ? 0.5 : 1)};
`;

const RightAlign = styled.div`
  display: flex;
  justify-content: flex-end;
  margin: -2px 0 8px;
`;

const Hint = styled.div`
  margin: -6px 0 8px;
  font-size: 13px;
  font-weight: 700;
  color: ${({ ok }) => (ok ? "#1cb04e" : "#e05252")};
`;

const GenderRow = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  margin: 6px 0 10px;
`;

const GenderBtn = styled.button`
  height: 42px;
  border-radius: 999px;
  border: 2px solid #ff2f79;
  background: ${({ $active }) => ($active ? "linear-gradient(180deg,#ff3f8a 0%, #ff2f79 100%)" : "#fff")};
  color: ${({ $active }) => ($active ? "#fff" : "#ff2f79")};
  font-weight: 800;
  cursor: pointer;
`;

const PrimaryBtn = styled.button`
  width: 100%;
  height: 46px;
  border-radius: 999px;
  border: 0;
  font-weight: 800;
  color: #fff;
  background: ${({ disabled }) => (disabled ? "#d9d9d9" : "linear-gradient(180deg,#ff3f8a 0%, #ff2f79 100%)")};
  box-shadow: ${({ disabled }) => (disabled ? "none" : "0 10px 22px rgba(255,47,121,.35)")};
  cursor: ${({ disabled }) => (disabled ? "not-allowed" : "pointer")};
  transition: transform .12s ease;
  margin-top: 8px;
  &:hover { transform: ${({ disabled }) => (disabled ? "none" : "translateY(-1px)")}; }
`;

const ModalBG = styled.div`
  position: fixed; inset: 0;
  background: rgba(0,0,0,.35);
  display: grid; place-items: center;
`;

const ModalCard = styled.div`
  background: #fff;
  padding: 18px 22px;
  border-radius: 12px;
  font-weight: 800;
`;
