// src/App.jsx
import "./lib/http";
import React, { useState } from "react";
import { BrowserRouter as Router, Routes, Route, useLocation } from "react-router-dom";
import { GlobalFonts } from "./styles/global-fonts";
import { Navigation } from "./components/navigation";
import { Testimonials } from "./components/testimonials";
import Ting from "./components/ting";     //   
import Login from "./components/login";
import Signup from "./components/signup";
import Main from "./components/main";
import Analysis from "./components/analysis";
import MyPage from "./components/mypage";
import RecentChats from "./components/recent-chats";
import SmoothScroll from "smooth-scroll";
import "./App.css";

//    (  )
export const scroll = new SmoothScroll('a[href*="#"]', {
  speed: 1000,
  speedAsDuration: true,
});

function AppInner() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [messages, setMessages] = useState([]);
  const location = useLocation();

  const handleLogin = () => setIsLoggedIn(true);
  const handleLogout = () => {
    setIsLoggedIn(false);
    setMessages([]); //    
  };

  //  //   
  const showNav = !["/", "/login", "/signup"].includes(location.pathname);

  return (
    <>
      {showNav && <Navigation isLoggedIn={isLoggedIn} onLogout={handleLogout} />}

      <Routes>
        {/*   : Ting */}
        <Route path="/" element={<Ting />} />

        {/* / */}
        <Route path="/login" element={<Login onLogin={handleLogin} />} />
        <Route path="/signup" element={<Signup />} />

        {/* /  */}
        <Route path="/main" element={<Main />} />   {/*   */}
        <Route path="/analysis" element={<Analysis />} />
        <Route path="/mypage" element={<MyPage />} />
        <Route path="/history" element={<RecentChats />} />
        <Route
          path="/chat"
          element={
            <Testimonials
              isLoggedIn={isLoggedIn}
              messages={messages}
              setMessages={setMessages}
            />
          }
        />
      </Routes>
    </>
  );
}

export default function App() {
  return (
    <Router>
      <GlobalFonts />
      <AppInner />
    </Router>
  );
}
